import { useEffect, useState } from "react";
import "./App.css";

import CharacterssData from "./components/CharacterssData";
import AppBarc from "./components/AppBarc";

import {  ThemeProvider, createTheme } from "@mui/material";

function App() {
  
  const [character, setCharacter] = useState([]);
  const [darkMode, setDarkMode] = useState("dark");

  const darkTheme = createTheme({
    palette: {
      mode: darkMode,
    },
  });

  async function getCharacters() {
    try {
      const character = await fetch(
        "https://rickandmortyapi.com/api/character",
        {
          method: "GET",
          // headers:'accept: application/json'
        }
      );
      const jsonCharacter = await character.json();
      console.log(jsonCharacter);
      setCharacter(jsonCharacter.results);
    } catch (err) {
      console.log("Soemthing went wrong ", err.message);
    }
  }

  async function getNextPage(pageNumber) {
    try {
      const character = await fetch(
        `https://rickandmortyapi.com/api/character/?page=${pageNumber}`,
        {
          method: "GET",
        }
      );
      const jsonCharacter = await character.json();
      // console.log(jsonCharacter);
      // console.log(jsonCharacter.results);
      setCharacter(jsonCharacter.results);
      console.log(character);
    } catch (err) {
      console.log("Soemthing went wrong ", err.message);
    }
  }

  useEffect(() => {
    getCharacters();
  }, []);

  return (
    <div className="App">
      <ThemeProvider theme={darkTheme}>
        <AppBarc setDarkMode={setDarkMode} />
        <CharacterssData
          character={character}
          getNextPage={getNextPage}
          setCharacter={setCharacter}
        />
      </ThemeProvider>
    </div>
  );
}

export default App;
