import React from "react";
import { AppBar, Grid, IconButton, Typography } from "@mui/material";
import { DarkMode } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import LogoutIcon from "@mui/icons-material/Logout";

function AppBarc(props) {
  const navigate = useNavigate();
  const [themeBool, setThemeBool] = React.useState(true);

  const handleDarkmode = () => {
   setThemeBool(!themeBool);

    if (themeBool === true) {
      props.setDarkMode("light");
    } else {
      props.setDarkMode("dark");
    }
  };

  return (
    <div>
      <Grid container>
        <Grid item xs={12}>
          <AppBar
            position="static"
            sx={{
              padding: 8,
                backgroundImage:
                  "URL(https://1000logos.net/wp-content/uploads/2022/03/Rick-and-Morty.png)",
                backgroundSize: "contain",
                backgroundPosition: "center",
                backgroundRepeat:"no-repeat",
            }}
          >
            <Grid container>
              <Grid item xs={2} />
              <Grid item xs={8}>
                <Typography variant="h1" fontWeight={700} >
              
                </Typography>
              </Grid>
              <Grid item xs={2} justifyItems={"center"}>
                <IconButton onClick={handleDarkmode}>
                  <DarkMode sx={{ color: "white" ,m:2}} />
                </IconButton>
                <IconButton onClick={() => navigate("/")}>
                  <LogoutIcon sx={{ color: "white",m:2}} />
                </IconButton>
              </Grid>
            </Grid>
          </AppBar>
        </Grid>
      </Grid>
    </div>
  );
}

export default AppBarc;
