import React, { useEffect, useState } from "react";
import MUIDataTable from "mui-datatables";
import { Avatar, Grid } from "@mui/material";

function CharacterssData({ character, setCharacter, getNextPage }) {
  const [Name, setname] = useState("");
  const [gender, setgender] = useState("");
  const [species, setspecies] = useState("");
  const [status, setstatus] = useState("");

  const data = character;

  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
      },
    },
    {
      name: "image",
      label: "Image",
      options: {
        filter: false,
        sort: false,
        customBodyRender: (value) => (
          <Avatar src={value} alt="char-image" sx={{ height: "80px", width: "80px" }} />
        ),
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "gender",
      label: "Gender",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "species",
      label: "Species",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: false,
      },
    },
  ];

  const handlefilter = React.useCallback(async () => {
    const data = await fetch(
      `https://rickandmortyapi.com/api/character/?gender=${gender}&name=${Name}&species=${species}&status=${status}`
    );
    const responsedata = await data.json();
    setCharacter([...responsedata.results]);
  }, [gender, species, Name, status, setCharacter]);

  const options = {
    serverSide: true,
    count: 826,
    rowsPerPage: 20,
    filter: true,
    confirmFilters: false,
    rowsPerPageOptions: [20, 25],
    onTableChange: (action, tablestate) => {
      if (action === "changePage") {
        const nextpage = tablestate.page + 1;
        getNextPage(nextpage);
      }
    },
    onFilterChange: (ChangedColumn, filterList) => {
      // console.log(ChangedColumn);
      // console.log(filterList);
      setname(filterList[0]);
      setgender(filterList[3]);
      setspecies(filterList[4]);
      setstatus(filterList[5]);
    },
  };

  useEffect(() => {
    handlefilter();
  }, [handlefilter]);

  return (
    <Grid
      container
      sx={{
        backgroundColor: "black",
        padding: 5,
        pb:10,
        backgroundImage: "URL(https://wallpaperaccess.com/full/238294.png)",
      }}
    >
      <Grid item xs={12}   >
        <MUIDataTable
          title={"Rick and Morty Characters"}
          data={data}
          columns={columns}
          options={options}
        />
      </Grid>
    </Grid>
  );
}

export default CharacterssData;
